import { ChainId, ENVChainId } from './constants'

const routerAddress = 
{
  [ChainId.BSCTESTNET]: '',

  [ChainId.BSCNET]: '',

  [ChainId.HTNET]: '0xed7d5f38c79115ca12fe6c0041abb22f0a06c300'
} 

export function getRouterAddress(chainId: ChainId)
{
  return routerAddress[chainId] ? routerAddress[chainId] : routerAddress[ENVChainId]
}
  
